/* CONTAINER */
.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 10px;
}

/* CONTAINER MIXEX */
.presentation_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* CONTAINER MEDIA */
@media screen and (min-width: 576px) {
}
@media screen and (min-width: 992px) {
  .presentation_container {
    flex-wrap: nowrap;
    justify-content: space-around;
  }
}
@media screen and (min-width: 1200px) {
}
