/* HEADER  */
.header {
  background-image: url('../../img/main-bg.jpg');
  background-position-x: 20%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.header:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: #000;
  opacity: 0.3;
  z-index: 0;
}
.header:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 80px;
  width: 100%;
  opacity: 0.5;
  background: #000;
  z-index: 9;
}

